//<br />// Dropdown menus<br />// --------------------------------------------------<br /><br /><br />// Dropdown arrow/caret<br />.caret {<br />  display: inline-block;<br />  width: 0;<br />  height: 0;<br />  margin-left: 2px;<br />  vertical-align: middle;<br />  border-top: @caret-width-base dashed;<br />  border-top: @caret-width-base solid ~"\9"; // IE8<br />  border-right: @caret-width-base solid transparent;<br />  border-left: @caret-width-base solid transparent;<br />}<br /><br />// The dropdown wrapper (div)<br />.dropup,<br />.dropdown {<br />  position: relative;<br />}<br /><br />// Prevent the focus on the dropdown toggle when closing dropdowns<br />.dropdown-toggle:focus {<br />  outline: 0;<br />}<br /><br />// The dropdown menu (ul)<br />.dropdown-menu {<br />  position: absolute;<br />  top: 100%;<br />  left: 0;<br />  z-index: @zindex-dropdown;<br />  display: none; // none by default, but block on "open" of the menu<br />  float: left;<br />  min-width: 160px;<br />  padding: 5px 0;<br />  margin: 2px 0 0; // override default ul<br />  font-size: @font-size-base;<br />  text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)<br />  list-style: none;<br />  background-color: @dropdown-bg;<br />  background-clip: padding-box;<br />  border: 1px solid @dropdown-fallback-border; // IE8 fallback<br />  border: 1px solid @dropdown-border;<br />  border-radius: @border-radius-base;<br />  .box-shadow(0 6px 12px rgba(0, 0, 0, .175));<br /><br />  // Aligns the dropdown menu to right<br />  //<br />  // Deprecated as of 3.1.0 in favor of `.dropdown-menu-[dir]`<br />  &.pull-right {<br />    right: 0;<br />    left: auto;<br />  }<br /><br />  // Dividers (basically an hr) within the dropdown<br />  .divider {<br />    .nav-divider(@dropdown-divider-bg);<br />  }<br /><br />  // Links within the dropdown menu<br />  > li > a {<br />    display: block;<br />    padding: 3px 20px;<br />    clear: both;<br />    font-weight: 400;<br />    line-height: @line-height-base;<br />    color: @dropdown-link-color;<br />    white-space: nowrap; // prevent links from randomly breaking onto new lines<br /><br />    &:hover,<br />    &:focus {<br />      color: @dropdown-link-hover-color;<br />      text-decoration: none;<br />      background-color: @dropdown-link-hover-bg;<br />    }<br />  }<br />}<br /><br />// Active state<br />.dropdown-menu > .active > a {<br />  &,<br />  &:hover,<br />  &:focus {<br />    color: @dropdown-link-active-color;<br />    text-decoration: none;<br />    background-color: @dropdown-link-active-bg;<br />    outline: 0;<br />  }<br />}<br /><br />// Disabled state<br />//<br />// Gray out text and ensure the hover/focus state remains gray<br /><br />.dropdown-menu > .disabled > a {<br />  &,<br />  &:hover,<br />  &:focus {<br />    color: @dropdown-link-disabled-color;<br />  }<br /><br />  // Nuke hover/focus effects<br />  &:hover,<br />  &:focus {<br />    text-decoration: none;<br />    cursor: @cursor-disabled;<br />    background-color: transparent;<br />    background-image: none; // Remove CSS gradient<br />    .reset-filter();<br />  }<br />}<br /><br />// Open state for the dropdown<br />.open {<br />  // Show the menu<br />  > .dropdown-menu {<br />    display: block;<br />  }<br /><br />  // Remove the outline when :focus is triggered<br />  > a {<br />    outline: 0;<br />  }<br />}<br /><br />// Menu positioning<br />//<br />// Add extra class to `.dropdown-menu` to flip the alignment of the dropdown<br />// menu with the parent.<br />.dropdown-menu-right {<br />  right: 0;<br />  left: auto; // Reset the default from `.dropdown-menu`<br />}<br />// With v3, we enabled auto-flipping if you have a dropdown within a right<br />// aligned nav component. To enable the undoing of that, we provide an override<br />// to restore the default dropdown menu alignment.<br />//<br />// This is only for left-aligning a dropdown menu within a `.navbar-right` or<br />// `.pull-right` nav component.<br />.dropdown-menu-left {<br />  right: auto;<br />  left: 0;<br />}<br /><br />// Dropdown section headers<br />.dropdown-header {<br />  display: block;<br />  padding: 3px 20px;<br />  font-size: @font-size-small;<br />  line-height: @line-height-base;<br />  color: @dropdown-header-color;<br />  white-space: nowrap; // as with > li > a<br />}<br /><br />// Backdrop to catch body clicks on mobile, etc.<br />.dropdown-backdrop {<br />  position: fixed;<br />  top: 0;<br />  right: 0;<br />  bottom: 0;<br />  left: 0;<br />  z-index: (@zindex-dropdown - 10);<br />}<br /><br />// Right aligned dropdowns<br />.pull-right > .dropdown-menu {<br />  right: 0;<br />  left: auto;<br />}<br /><br />// Allow for dropdowns to go bottom up (aka, dropup-menu)<br />//<br />// Just add .dropup after the standard .dropdown class and you're set, bro.<br />// TODO: abstract this so that the navbar fixed styles are not placed here?<br /><br />.dropup,<br />.navbar-fixed-bottom .dropdown {<br />  // Reverse the caret<br />  .caret {<br />    content: "";<br />    border-top: 0;<br />    border-bottom: @caret-width-base dashed;<br />    border-bottom: @caret-width-base solid ~"\9"; // IE8<br />  }<br />  // Different positioning for bottom up menu<br />  .dropdown-menu {<br />    top: auto;<br />    bottom: 100%;<br />    margin-bottom: 2px;<br />  }<br />}<br /><br /><br />// Component alignment<br />//<br />// Reiterate per navbar.less and the modified component alignment there.<br /><br />@media (min-width: @grid-float-breakpoint) {<br />  .navbar-right {<br />    .dropdown-menu {<br />      .dropdown-menu-right();<br />    }<br />    // Necessary for overrides of the default right aligned menu.<br />    // Will remove come v4 in all likelihood.<br />    .dropdown-menu-left {<br />      .dropdown-menu-left();<br />    }<br />  }<br />}<br />