//<br />// Grid system<br />// --------------------------------------------------<br /><br /><br />// Container widths<br />//<br />// Set the container width, and override it for fixed navbars in media queries.<br /><br />.container {<br />  .container-fixed();<br /><br />  @media (min-width: @screen-sm-min) {<br />    width: @container-sm;<br />  }<br />  @media (min-width: @screen-md-min) {<br />    width: @container-md;<br />  }<br />  @media (min-width: @screen-lg-min) {<br />    width: @container-lg;<br />  }<br />}<br /><br /><br />// Fluid container<br />//<br />// Utilizes the mixin meant for fixed width containers, but without any defined<br />// width for fluid, full width layouts.<br /><br />.container-fluid {<br />  .container-fixed();<br />}<br /><br /><br />// Row<br />//<br />// Rows contain and clear the floats of your columns.<br /><br />.row {<br />  .make-row();<br />}<br /><br />.row-no-gutters {<br />  margin-right: 0;<br />  margin-left: 0;<br /><br />  [class*="col-"] {<br />    padding-right: 0;<br />    padding-left: 0;<br />  }<br />}<br /><br /><br />// Columns<br />//<br />// Common styles for small and large grid columns<br /><br />.make-grid-columns();<br /><br /><br />// Extra small grid<br />//<br />// Columns, offsets, pushes, and pulls for extra small devices like<br />// smartphones.<br /><br />.make-grid(xs);<br /><br /><br />// Small grid<br />//<br />// Columns, offsets, pushes, and pulls for the small device range, from phones<br />// to tablets.<br /><br />@media (min-width: @screen-sm-min) {<br />  .make-grid(sm);<br />}<br /><br /><br />// Medium grid<br />//<br />// Columns, offsets, pushes, and pulls for the desktop device range.<br /><br />@media (min-width: @screen-md-min) {<br />  .make-grid(md);<br />}<br /><br /><br />// Large grid<br />//<br />// Columns, offsets, pushes, and pulls for the large desktop device range.<br /><br />@media (min-width: @screen-lg-min) {<br />  .make-grid(lg);<br />}<br />