//<br />// Code (inline and block)<br />// --------------------------------------------------<br /><br /><br />// Inline and block code styles<br />code,<br />kbd,<br />pre,<br />samp {<br />  font-family: @font-family-monospace;<br />}<br /><br />// Inline code<br />code {<br />  padding: 2px 4px;<br />  font-size: 90%;<br />  color: @code-color;<br />  background-color: @code-bg;<br />  border-radius: @border-radius-base;<br />}<br /><br />// User input typically entered via keyboard<br />kbd {<br />  padding: 2px 4px;<br />  font-size: 90%;<br />  color: @kbd-color;<br />  background-color: @kbd-bg;<br />  border-radius: @border-radius-small;<br />  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .25);<br /><br />  kbd {<br />    padding: 0;<br />    font-size: 100%;<br />    font-weight: 700;<br />    box-shadow: none;<br />  }<br />}<br /><br />// Blocks of code<br />pre {<br />  display: block;<br />  padding: ((@line-height-computed - 1) / 2);<br />  margin: 0 0 (@line-height-computed / 2);<br />  font-size: (@font-size-base - 1); // 14px to 13px<br />  line-height: @line-height-base;<br />  color: @pre-color;<br />  word-break: break-all;<br />  word-wrap: break-word;<br />  background-color: @pre-bg;<br />  border: 1px solid @pre-border-color;<br />  border-radius: @border-radius-base;<br /><br />  // Account for some code outputs that place code tags in pre tags<br />  code {<br />    padding: 0;<br />    font-size: inherit;<br />    color: inherit;<br />    white-space: pre-wrap;<br />    background-color: transparent;<br />    border-radius: 0;<br />  }<br />}<br /><br />// Enable scrollable blocks of code<br />.pre-scrollable {<br />  max-height: @pre-scrollable-max-height;<br />  overflow-y: scroll;<br />}<br />