//<br />// Jumbotron<br />// --------------------------------------------------<br /><br /><br />.jumbotron {<br />  padding-top: @jumbotron-padding;<br />  padding-bottom: @jumbotron-padding;<br />  margin-bottom: @jumbotron-padding;<br />  color: @jumbotron-color;<br />  background-color: @jumbotron-bg;<br /><br />  h1,<br />  .h1 {<br />    color: @jumbotron-heading-color;<br />  }<br /><br />  p {<br />    margin-bottom: (@jumbotron-padding / 2);<br />    font-size: @jumbotron-font-size;<br />    font-weight: 200;<br />  }<br /><br />  > hr {<br />    border-top-color: darken(@jumbotron-bg, 10%);<br />  }<br /><br />  .container &,<br />  .container-fluid & {<br />    padding-right: (@grid-gutter-width / 2);<br />    padding-left: (@grid-gutter-width / 2);<br />    border-radius: @border-radius-large; // Only round corners at higher resolutions if contained in a container<br />  }<br /><br />  .container {<br />    max-width: 100%;<br />  }<br /><br />  @media screen and (min-width: @screen-sm-min) {<br />    padding-top: (@jumbotron-padding * 1.6);<br />    padding-bottom: (@jumbotron-padding * 1.6);<br /><br />    .container &,<br />    .container-fluid & {<br />      padding-right: (@jumbotron-padding * 2);<br />      padding-left: (@jumbotron-padding * 2);<br />    }<br /><br />    h1,<br />    .h1 {<br />      font-size: @jumbotron-heading-font-size;<br />    }<br />  }<br />}<br />