// Form validation states<br />//<br />// Used in forms.less to generate the form validation CSS for warnings, errors,<br />// and successes.<br /><br />.form-control-validation(@text-color: #555; @border-color: #ccc; @background-color: #f5f5f5) {<br />  // Color the label and help text<br />  .help-block,<br />  .control-label,<br />  .radio,<br />  .checkbox,<br />  .radio-inline,<br />  .checkbox-inline,<br />  &.radio label,<br />  &.checkbox label,<br />  &.radio-inline label,<br />  &.checkbox-inline label  {<br />    color: @text-color;<br />  }<br />  // Set the border and box shadow on specific inputs to match<br />  .form-control {<br />    border-color: @border-color;<br />    .box-shadow(inset 0 1px 1px rgba(0, 0, 0, .075)); // Redeclare so transitions work<br />    &:focus {<br />      border-color: darken(@border-color, 10%);<br />      @shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px lighten(@border-color, 20%);<br />      .box-shadow(@shadow);<br />    }<br />  }<br />  // Set validation states also for addons<br />  .input-group-addon {<br />    color: @text-color;<br />    background-color: @background-color;<br />    border-color: @border-color;<br />  }<br />  // Optional feedback icon<br />  .form-control-feedback {<br />    color: @text-color;<br />  }<br />}<br /><br /><br />// Form control focus state<br />//<br />// Generate a customized focus state and for any input with the specified color,<br />// which defaults to the `@input-border-focus` variable.<br />//<br />// We highly encourage you to not customize the default value, but instead use<br />// this to tweak colors on an as-needed basis. This aesthetic change is based on<br />// WebKit's default styles, but applicable to a wider range of browsers. Its<br />// usability and accessibility should be taken into account with any change.<br />//<br />// Example usage: change the default blue border and shadow to white for better<br />// contrast against a dark gray background.<br />.form-control-focus(@color: @input-border-focus) {<br />  @color-rgba: rgba(red(@color), green(@color), blue(@color), .6);<br />  &:focus {<br />    border-color: @color;<br />    outline: 0;<br />    .box-shadow(~"inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px @{color-rgba}");<br />  }<br />}<br /><br />// Form control sizing<br />//<br />// Relative text size, padding, and border-radii changes for form controls. For<br />// horizontal sizing, wrap controls in the predefined grid classes. `<select>`<br />// element gets special love because it's special, and that's a fact!<br />.input-size(@input-height; @padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius) {<br />  height: @input-height;<br />  padding: @padding-vertical @padding-horizontal;<br />  font-size: @font-size;<br />  line-height: @line-height;<br />  border-radius: @border-radius;<br /><br />  select& {<br />    height: @input-height;<br />    line-height: @input-height;<br />  }<br /><br />  textarea&,<br />  select[multiple]& {<br />    height: auto;<br />  }<br />}<br />