// stylelint-disable selector-no-qualifying-type, selector-max-type<br /><br />//<br />// Navs<br />// --------------------------------------------------<br /><br /><br />// Base class<br />// --------------------------------------------------<br /><br />.nav {<br />  padding-left: 0; // Override default ul/ol<br />  margin-bottom: 0;<br />  list-style: none;<br />  &:extend(.clearfix all);<br /><br />  > li {<br />    position: relative;<br />    display: block;<br /><br />    > a {<br />      position: relative;<br />      display: block;<br />      padding: @nav-link-padding;<br />      &:hover,<br />      &:focus {<br />        text-decoration: none;<br />        background-color: @nav-link-hover-bg;<br />      }<br />    }<br /><br />    // Disabled state sets text to gray and nukes hover/tab effects<br />    &.disabled > a {<br />      color: @nav-disabled-link-color;<br /><br />      &:hover,<br />      &:focus {<br />        color: @nav-disabled-link-hover-color;<br />        text-decoration: none;<br />        cursor: @cursor-disabled;<br />        background-color: transparent;<br />      }<br />    }<br />  }<br /><br />  // Open dropdowns<br />  .open > a {<br />    &,<br />    &:hover,<br />    &:focus {<br />      background-color: @nav-link-hover-bg;<br />      border-color: @link-color;<br />    }<br />  }<br /><br />  // Nav dividers (deprecated with v3.0.1)<br />  //<br />  // This should have been removed in v3 with the dropping of `.nav-list`, but<br />  // we missed it. We don't currently support this anywhere, but in the interest<br />  // of maintaining backward compatibility in case you use it, it's deprecated.<br />  .nav-divider {<br />    .nav-divider();<br />  }<br /><br />  // Prevent IE8 from misplacing imgs<br />  //<br />  // See https://github.com/h5bp/html5-boilerplate/issues/984#issuecomment-3985989<br />  > li > a > img {<br />    max-width: none;<br />  }<br />}<br /><br /><br />// Tabs<br />// -------------------------<br /><br />// Give the tabs something to sit on<br />.nav-tabs {<br />  border-bottom: 1px solid @nav-tabs-border-color;<br />  > li {<br />    float: left;<br />    // Make the list-items overlay the bottom border<br />    margin-bottom: -1px;<br /><br />    // Actual tabs (as links)<br />    > a {<br />      margin-right: 2px;<br />      line-height: @line-height-base;<br />      border: 1px solid transparent;<br />      border-radius: @border-radius-base @border-radius-base 0 0;<br />      &:hover {<br />        border-color: @nav-tabs-link-hover-border-color @nav-tabs-link-hover-border-color @nav-tabs-border-color;<br />      }<br />    }<br /><br />    // Active state, and its :hover to override normal :hover<br />    &.active > a {<br />      &,<br />      &:hover,<br />      &:focus {<br />        color: @nav-tabs-active-link-hover-color;<br />        cursor: default;<br />        background-color: @nav-tabs-active-link-hover-bg;<br />        border: 1px solid @nav-tabs-active-link-hover-border-color;<br />        border-bottom-color: transparent;<br />      }<br />    }<br />  }<br />  // pulling this in mainly for less shorthand<br />  &.nav-justified {<br />    .nav-justified();<br />    .nav-tabs-justified();<br />  }<br />}<br /><br /><br />// Pills<br />// -------------------------<br />.nav-pills {<br />  > li {<br />    float: left;<br /><br />    // Links rendered as pills<br />    > a {<br />      border-radius: @nav-pills-border-radius;<br />    }<br />    + li {<br />      margin-left: 2px;<br />    }<br /><br />    // Active state<br />    &.active > a {<br />      &,<br />      &:hover,<br />      &:focus {<br />        color: @nav-pills-active-link-hover-color;<br />        background-color: @nav-pills-active-link-hover-bg;<br />      }<br />    }<br />  }<br />}<br /><br /><br />// Stacked pills<br />.nav-stacked {<br />  > li {<br />    float: none;<br />    + li {<br />      margin-top: 2px;<br />      margin-left: 0; // no need for this gap between nav items<br />    }<br />  }<br />}<br /><br /><br />// Nav variations<br />// --------------------------------------------------<br /><br />// Justified nav links<br />// -------------------------<br /><br />.nav-justified {<br />  width: 100%;<br /><br />  > li {<br />    float: none;<br />    > a {<br />      margin-bottom: 5px;<br />      text-align: center;<br />    }<br />  }<br /><br />  > .dropdown .dropdown-menu {<br />    top: auto;<br />    left: auto;<br />  }<br /><br />  @media (min-width: @screen-sm-min) {<br />    > li {<br />      display: table-cell;<br />      width: 1%;<br />      > a {<br />        margin-bottom: 0;<br />      }<br />    }<br />  }<br />}<br /><br />// Move borders to anchors instead of bottom of list<br />//<br />// Mixin for adding on top the shared `.nav-justified` styles for our tabs<br />.nav-tabs-justified {<br />  border-bottom: 0;<br /><br />  > li > a {<br />    // Override margin from .nav-tabs<br />    margin-right: 0;<br />    border-radius: @border-radius-base;<br />  }<br /><br />  > .active > a,<br />  > .active > a:hover,<br />  > .active > a:focus {<br />    border: 1px solid @nav-tabs-justified-link-border-color;<br />  }<br /><br />  @media (min-width: @screen-sm-min) {<br />    > li > a {<br />      border-bottom: 1px solid @nav-tabs-justified-link-border-color;<br />      border-radius: @border-radius-base @border-radius-base 0 0;<br />    }<br />    > .active > a,<br />    > .active > a:hover,<br />    > .active > a:focus {<br />      border-bottom-color: @nav-tabs-justified-active-link-border-color;<br />    }<br />  }<br />}<br /><br /><br />// Tabbable tabs<br />// -------------------------<br /><br />// Hide tabbable panes to start, show them when `.active`<br />.tab-content {<br />  > .tab-pane {<br />    display: none;<br />  }<br />  > .active {<br />    display: block;<br />  }<br />}<br /><br /><br />// Dropdowns<br />// -------------------------<br /><br />// Specific dropdowns<br />.nav-tabs .dropdown-menu {<br />  // make dropdown border overlap tab border<br />  margin-top: -1px;<br />  // Remove the top rounded corners here since there is a hard edge above the menu<br />  .border-top-radius(0);<br />}<br />