// stylelint-disable selector-no-qualifying-type<br /><br />//<br />// List groups<br />// --------------------------------------------------<br /><br /><br />// Base class<br />//<br />// Easily usable on <ul>, <ol>, or <div>.<br /><br />.list-group {<br />  // No need to set list-style: none; since .list-group-item is block level<br />  padding-left: 0; // reset padding because ul and ol<br />  margin-bottom: 20px;<br />}<br /><br /><br />// Individual list items<br />//<br />// Use on `li`s or `div`s within the `.list-group` parent.<br /><br />.list-group-item {<br />  position: relative;<br />  display: block;<br />  padding: 10px 15px;<br />  // Place the border on the list items and negative margin up for better styling<br />  margin-bottom: -1px;<br />  background-color: @list-group-bg;<br />  border: 1px solid @list-group-border;<br /><br />  // Round the first and last items<br />  &:first-child {<br />    .border-top-radius(@list-group-border-radius);<br />  }<br />  &:last-child {<br />    margin-bottom: 0;<br />    .border-bottom-radius(@list-group-border-radius);<br />  }<br /><br />  // Disabled state<br />  &.disabled,<br />  &.disabled:hover,<br />  &.disabled:focus {<br />    color: @list-group-disabled-color;<br />    cursor: @cursor-disabled;<br />    background-color: @list-group-disabled-bg;<br /><br />    // Force color to inherit for custom content<br />    .list-group-item-heading {<br />      color: inherit;<br />    }<br />    .list-group-item-text {<br />      color: @list-group-disabled-text-color;<br />    }<br />  }<br /><br />  // Active class on item itself, not parent<br />  &.active,<br />  &.active:hover,<br />  &.active:focus {<br />    z-index: 2; // Place active items above their siblings for proper border styling<br />    color: @list-group-active-color;<br />    background-color: @list-group-active-bg;<br />    border-color: @list-group-active-border;<br /><br />    // Force color to inherit for custom content<br />    .list-group-item-heading,<br />    .list-group-item-heading > small,<br />    .list-group-item-heading > .small {<br />      color: inherit;<br />    }<br />    .list-group-item-text {<br />      color: @list-group-active-text-color;<br />    }<br />  }<br />}<br /><br /><br />// Interactive list items<br />//<br />// Use anchor or button elements instead of `li`s or `div`s to create interactive items.<br />// Includes an extra `.active` modifier class for showing selected items.<br /><br />a.list-group-item,<br />button.list-group-item {<br />  color: @list-group-link-color;<br /><br />  .list-group-item-heading {<br />    color: @list-group-link-heading-color;<br />  }<br /><br />  // Hover state<br />  &:hover,<br />  &:focus {<br />    color: @list-group-link-hover-color;<br />    text-decoration: none;<br />    background-color: @list-group-hover-bg;<br />  }<br />}<br /><br />button.list-group-item {<br />  width: 100%;<br />  text-align: left;<br />}<br /><br /><br />// Contextual variants<br />//<br />// Add modifier classes to change text and background color on individual items.<br />// Organizationally, this must come after the `:hover` states.<br /><br />.list-group-item-variant(success; @state-success-bg; @state-success-text);<br />.list-group-item-variant(info; @state-info-bg; @state-info-text);<br />.list-group-item-variant(warning; @state-warning-bg; @state-warning-text);<br />.list-group-item-variant(danger; @state-danger-bg; @state-danger-text);<br /><br /><br />// Custom content options<br />//<br />// Extra classes for creating well-formatted content within `.list-group-item`s.<br /><br />.list-group-item-heading {<br />  margin-top: 0;<br />  margin-bottom: 5px;<br />}<br />.list-group-item-text {<br />  margin-bottom: 0;<br />  line-height: 1.3;<br />}<br />