ody {<br />    border-top: 2px solid @table-border-color;<br />  }<br /><br />  // Nesting<br />  .table {<br />    background-color: @body-bg;<br />  }<br />}<br /><br /><br />// Condensed table w/ half padding<br /><br />.table-condensed {<br />  > thead,<br />  > tbody,<br />  > tfoot {<br />    > tr {<br />      > th,<br />      > td {<br />        padding: @table-condensed-cell-padding;<br />      }<br />    }<br />  }<br />}<br /><br /><br />// Bordered version<br />//<br />// Add borders all around the table and between all the columns.<br /><br />.table-bordered {<br />  border: 1px solid @table-border-color;<br />  > thead,<br />  > tbody,<br />  > tfoot {<br />    > tr {<br />      > th,<br />      > td {<br />        border: 1px solid @table-border-color;<br />      }<br />    }<br />  }<br />  > thead > tr {<br />    > th,<br />    > td {<br />      border-bottom-width: 2px;<br />    }<br />  }<br />}<br /><br /><br />// Zebra-striping<br />//<br />// Default zebra-stripe styles (alternating gray and transparent backgrounds)<br /><br />.table-striped {<br />  > tbody > tr:nth-of-type(odd) {<br />    background-color: @table-bg-accent;<br />  }<br />}<br /><br /><br />// Hover effect<br />//<br />// Placed here since it has to come after the potential zebra striping<br /><br />.table-hover {<br />  > tbody > tr:hover {<br />    background-color: @table-bg-hover;<br />  }<br />}<br /><br /><br />// Table backgrounds<br />//<br />// Exact selectors below required to override `.table-striped` and prevent<br />// inheritance to nested tables.<br /><br />// Generate the contextual variants<br />.table-row-variant(active; @table-bg-active);<br />.table-row-variant(success; @state-success-bg);<br />.table-row-variant(info; @state-info-bg);<br />.table-row-variant(warning; @state-warning-bg);<br />.table-row-variant(danger; @state-danger-bg);<br /><br /><br />// Responsive tables<br />//<br />// Wrap your tables in `.table-responsive` and we'll make them mobile friendly<br />// by enabling horizontal scrolling. Only applies <768px. Everything above that<br />// will display normally.<br /><br />.table-responsive {<br />  min-height: .01%; // Workaround for IE9 bug (see https://github.com/twbs/bootstrap/issues/14837)<br />  overflow-x: auto;<br /><br />  @media screen and (max-width: @screen-xs-max) {<br />    width: 100%;<br />    margin-bottom: (@line-height-computed * .75);<br />    overflow-y: hidden;<br />    -ms-overflow-style: -ms-autohiding-scrollbar;<br />    border: 1px solid @table-border-color;<br /><br />    // Tighten up spacing<br />    > .table {<br />      margin-bottom: 0;<br /><br />      // Ensure the content doesn't wrap<br />      > thead,<br />      > tbody,<br />      > tfoot {<br />        > tr {<br />          > th,<br />          > td {<br />            white-space: nowrap;<br />          }<br />        }<br />      }<br />    }<br /><br />    // Special overrides for the bordered tables<br />    > .table-bordered {<br />      border: 0;<br /><br />      // Nuke the appropriate borders so that the parent can handle them<br />      > thead,<br />      > tbody,<br />      > tfoot {<br />        > tr {<br />          > th:first-child,<br />          > td:first-child {<br />            border-left: 0;<br />          }<br />          > th:last-child,<br />          > td:last-child {<br />            border-right: 0;<br />          }<br />        }<br />      }<br /><br />      // Only nuke the last row's bottom-border in `tbody` and `tfoot` since<br />      // chances are there will be only one `tr` in a `thead` and that would<br />      // remove the border altogether.<br />      > tbody,<br />      > tfoot {<br />        > tr:last-child {<br />          > th,<br />          > td {<br />            border-bottom: 0;<br />          }<br />        }<br />      }<br /><br />    }<br />  }<br />}<br />