// Button variants<br />//<br />// Easily pump out default styles, as well as :hover, :focus, :active,<br />// and disabled options for all buttons<br /><br />.button-variant(@color; @background; @border) {<br />  color: @color;<br />  background-color: @background;<br />  border-color: @border;<br /><br />  &:focus,<br />  &.focus {<br />    color: @color;<br />    background-color: darken(@background, 10%);<br />    border-color: darken(@border, 25%);<br />  }<br />  &:hover {<br />    color: @color;<br />    background-color: darken(@background, 10%);<br />    border-color: darken(@border, 12%);<br />  }<br />  &:active,<br />  &.active,<br />  .open > .dropdown-toggle& {<br />    color: @color;<br />    background-color: darken(@background, 10%);<br />    background-image: none;<br />    border-color: darken(@border, 12%);<br /><br />    &:hover,<br />    &:focus,<br />    &.focus {<br />      color: @color;<br />      background-color: darken(@background, 17%);<br />      border-color: darken(@border, 25%);<br />    }<br />  }<br />  &.disabled,<br />  &[disabled],<br />  fieldset[disabled] & {<br />    &:hover,<br />    &:focus,<br />    &.focus {<br />      background-color: @background;<br />      border-color: @border;<br />    }<br />  }<br /><br />  .badge {<br />    color: @background;<br />    background-color: @color;<br />  }<br />}<br /><br />// Button sizes<br />.button-size(@padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius) {<br />  padding: @padding-vertical @padding-horizontal;<br />  font-size: @font-size;<br />  line-height: @line-height;<br />  border-radius: @border-radius;<br />}<br />