// stylelint-disable selector-no-qualifying-type<br /><br />//<br />// Buttons<br />// --------------------------------------------------<br /><br /><br />// Base styles<br />// --------------------------------------------------<br /><br />.btn {<br />  display: inline-block;<br />  margin-bottom: 0; // For input.btn<br />  font-weight: @btn-font-weight;<br />  text-align: center;<br />  white-space: nowrap;<br />  vertical-align: middle;<br />  touch-action: manipulation;<br />  cursor: pointer;<br />  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214<br />  border: 1px solid transparent;<br />  .button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; @line-height-base; @btn-border-radius-base);<br />  .user-select(none);<br /><br />  &,<br />  &:active,<br />  &.active {<br />    &:focus,<br />    &.focus {<br />      .tab-focus();<br />    }<br />  }<br /><br />  &:hover,<br />  &:focus,<br />  &.focus {<br />    color: @btn-default-color;<br />    text-decoration: none;<br />  }<br /><br />  &:active,<br />  &.active {<br />    background-image: none;<br />    outline: 0;<br />    .box-shadow(inset 0 3px 5px rgba(0, 0, 0, .125));<br />  }<br /><br />  &.disabled,<br />  &[disabled],<br />  fieldset[disabled] & {<br />    cursor: @cursor-disabled;<br />    .opacity(.65);<br />    .box-shadow(none);<br />  }<br /><br />  a& {<br />    &.disabled,<br />    fieldset[disabled] & {<br />      pointer-events: none; // Future-proof disabling of clicks on `<a>` elements<br />    }<br />  }<br />}<br /><br /><br />// Alternate buttons<br />// --------------------------------------------------<br /><br />.btn-default {<br />  .button-variant(@btn-default-color; @btn-default-bg; @btn-default-border);<br />}<br />.btn-primary {<br />  .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);<br />}<br />// Success appears as green<br />.btn-success {<br />  .button-variant(@btn-success-color; @btn-success-bg; @btn-success-border);<br />}<br />// Info appears as blue-green<br />.btn-info {<br />  .button-variant(@btn-info-color; @btn-info-bg; @btn-info-border);<br />}<br />// Warning appears as orange<br />.btn-warning {<br />  .button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-border);<br />}<br />// Danger and error appear as red<br />.btn-danger {<br />  .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);<br />}<br /><br /><br />// Link buttons<br />// -------------------------<br /><br />// Make a button look and behave like a link<br />.btn-link {<br />  font-weight: 400;<br />  color: @link-color;<br />  border-radius: 0;<br /><br />  &,<br />  &:active,<br />  &.active,<br />  &[disabled],<br />  fieldset[disabled] & {<br />    background-color: transparent;<br />    .box-shadow(none);<br />  }<br />  &,<br />  &:hover,<br />  &:focus,<br />  &:active {<br />    border-color: transparent;<br />  }<br />  &:hover,<br />  &:focus {<br />    color: @link-hover-color;<br />    text-decoration: @link-hover-decoration;<br />    background-color: transparent;<br />  }<br />  &[disabled],<br />  fieldset[disabled] & {<br />    &:hover,<br />    &:focus {<br />      color: @btn-link-disabled-color;<br />      text-decoration: none;<br />    }<br />  }<br />}<br /><br /><br />// Button Sizes<br />// --------------------------------------------------<br /><br />.btn-lg {<br />  // line-height: ensure even-numbered height of button next to large input<br />  .button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @btn-border-radius-large);<br />}<br />.btn-sm {<br />  // line-height: ensure proper height of button next to small input<br />  .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @btn-border-radius-small);<br />}<br />.btn-xs {<br />  .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @btn-border-radius-small);<br />}<br /><br /><br />// Block button<br />// --------------------------------------------------<br /><br />.btn-block {<br />  display: block;<br />  width: 100%;<br />}<br /><br />// Vertically space out multiple block buttons<br />.btn-block + .btn-block {<br />  margin-top: 5px;<br />}<br /><br />// Specificity overrides<br />input[type="submit"],<br />input[type="reset"],<br />input[type="button"] {<br />  &.btn-block {<br />    width: 100%;<br />  }<br />}<br />