// stylelint-disable selector-list-comma-newline-after, selector-no-qualifying-type<br /><br />//<br />// Typography<br />// --------------------------------------------------<br /><br /><br />// Headings<br />// -------------------------<br /><br />h1, h2, h3, h4, h5, h6,<br />.h1, .h2, .h3, .h4, .h5, .h6 {<br />  font-family: @headings-font-family;<br />  font-weight: @headings-font-weight;<br />  line-height: @headings-line-height;<br />  color: @headings-color;<br /><br />  small,<br />  .small {<br />    font-weight: 400;<br />    line-height: 1;<br />    color: @headings-small-color;<br />  }<br />}<br /><br />h1, .h1,<br />h2, .h2,<br />h3, .h3 {<br />  margin-top: @line-height-computed;<br />  margin-bottom: (@line-height-computed / 2);<br /><br />  small,<br />  .small {<br />    font-size: 65%;<br />  }<br />}<br />h4, .h4,<br />h5, .h5,<br />h6, .h6 {<br />  margin-top: (@line-height-computed / 2);<br />  margin-bottom: (@line-height-computed / 2);<br /><br />  small,<br />  .small {<br />    font-size: 75%;<br />  }<br />}<br /><br />h1, .h1 { font-size: @font-size-h1; }<br />h2, .h2 { font-size: @font-size-h2; }<br />h3, .h3 { font-size: @font-size-h3; }<br />h4, .h4 { font-size: @font-size-h4; }<br />h5, .h5 { font-size: @font-size-h5; }<br />h6, .h6 { font-size: @font-size-h6; }<br /><br /><br />// Body text<br />// -------------------------<br /><br />p {<br />  margin: 0 0 (@line-height-computed / 2);<br />}<br /><br />.lead {<br />  margin-bottom: @line-height-computed;<br />  font-size: floor((@font-size-base * 1.15));<br />  font-weight: 300;<br />  line-height: 1.4;<br /><br />  @media (min-width: @screen-sm-min) {<br />    font-size: (@font-size-base * 1.5);<br />  }<br />}<br /><br /><br />// Emphasis & misc<br />// -------------------------<br /><br />// Ex: (12px small font / 14px base font) * 100% = about 85%<br />small,<br />.small {<br />  font-size: floor((100% * @font-size-small / @font-size-base));<br />}<br /><br />mark,<br />.mark {<br />  padding: .2em;<br />  background-color: @state-warning-bg;<br />}<br /><br />// Alignment<br />.text-left           { text-align: left; }<br />.text-right          { text-align: right; }<br />.text-center         { text-align: center; }<br />.text-justify        { text-align: justify; }<br />.text-nowrap         { white-space: nowrap; }<br /><br />// Transformation<br />.text-lowercase      { text-transform: lowercase; }<br />.text-uppercase      { text-transform: uppercase; }<br />.text-capitalize     { text-transform: capitalize; }<br /><br />// Contextual colors<br />.text-muted {<br />  color: @text-muted;<br />}<br />.text-primary {<br />  .text-emphasis-variant(@brand-primary);<br />}<br />.text-success {<br />  .text-emphasis-variant(@state-success-text);<br />}<br />.text-info {<br />  .text-emphasis-variant(@state-info-text);<br />}<br />.text-warning {<br />  .text-emphasis-variant(@state-warning-text);<br />}<br />.text-danger {<br />  .text-emphasis-variant(@state-danger-text);<br />}<br /><br />// Contextual backgrounds<br />// For now we'll leave these alongside the text classes until v4 when we can<br />// safely shift things around (per SemVer rules).<br />.bg-primary {<br />  // Given the contrast here, this is the only class to have its color inverted<br />  // automatically.<br />  color: #fff;<br />  .bg-variant(@brand-primary);<br />}<br />.bg-success {<br />  .bg-variant(@state-success-bg);<br />}<br />.bg-info {<br />  .bg-variant(@state-info-bg);<br />}<br />.bg-warning {<br />  .bg-variant(@state-warning-bg);<br />}<br />.bg-danger {<br />  .bg-variant(@state-danger-bg);<br />}<br /><br /><br />// Page header<br />// -------------------------<br /><br />.page-header {<br />  padding-bottom: ((@line-height-computed / 2) - 1);<br />  margin: (@line-height-computed * 2) 0 @line-height-computed;<br />  border-bottom: 1px solid @page-header-border-color;<br />}<br /><br /><br />// Lists<br />// -------------------------<br /><br />// Unordered and Ordered lists<br />ul,<br />ol {<br />  margin-top: 0;<br />  margin-bottom: (@line-height-computed / 2);<br />  ul,<br />  ol {<br />    margin-bottom: 0;<br />  }<br />}<br /><br />// List options<br /><br />// Unstyled keeps list items block level, just removes default browser padding and list-style<br />.list-unstyled {<br />  padding-left: 0;<br />  list-style: none;<br />}<br /><br />// Inline turns list items into inline-block<br />.list-inline {<br />  .list-unstyled();<br />  margin-left: -5px;<br /><br />  > li {<br />    display: inline-block;<br />    padding-right: 5px;<br />    padding-left: 5px;<br />  }<br />}<br /><br />// Description Lists<br />dl {<br />  margin-top: 0; // Remove browser default<br />  margin-bottom: @line-height-computed;<br />}<br />dt,<br />dd {<br />  line-height: @line-height-base;<br />}<br />dt {<br />  font-weight: 700;<br />}<br />dd {<br />  margin-left: 0; // Undo browser default<br />}<br /><br />// Horizontal description lists<br />//<br />// Defaults to being stacked without any of the below styles applied, until the<br />// grid breakpoint is reached (default of ~768px).<br /><br />.dl-horizontal {<br />  dd {<br />    &:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present<br />  }<br /><br />  @media (min-width: @dl-horizontal-breakpoint) {<br />    dt {<br />      float: left;<br />      width: (@dl-horizontal-offset - 20);<br />      clear: left;<br />      text-align: right;<br />      .text-overflow();<br />    }<br />    dd {<br />      margin-left: @dl-horizontal-offset;<br />    }<br />  }<br />}<br /><br /><br />// Misc<br />// -------------------------<br /><br />// Abbreviations and acronyms<br />// Add data-* attribute to help out our tooltip plugin, per https://github.com/twbs/bootstrap/issues/5257<br />abbr[title],<br />abbr[data-original-title] {<br />  cursor: help;<br />}<br /><br />.initialism {<br />  font-size: 90%;<br />  .text-uppercase();<br />}<br /><br />// Blockquotes<br />blockquote {<br />  padding: (@line-height-computed / 2) @line-height-computed;<br />  margin: 0 0 @line-height-computed;<br />  font-size: @blockquote-font-size;<br />  border-left: 5px solid @blockquote-border-color;<br /><br />  p,<br />  ul,<br />  ol {<br />    &:last-child {<br />      margin-bottom: 0;<br />    }<br />  }<br /><br />  // Note: Deprecated small and .small as of v3.1.0<br />  // Context: https://github.com/twbs/bootstrap/issues/11660<br />  footer,<br />  small,<br />  .small {<br />    display: block;<br />    font-size: 80%; // back to default font-size<br />    line-height: @line-height-base;<br />    color: @blockquote-small-color;<br /><br />    &:before {<br />      content: "\2014 \00A0"; // em dash, nbsp<br />    }<br />  }<br />}<br /><br />// Opposite alignment of blockquote<br />//<br />// Heads up: `blockquote.pull-right` has been deprecated as of v3.1.0.<br />.blockquote-reverse,<br />blockquote.pull-right {<br />  padding-right: 15px;<br />  padding-left: 0;<br />  text-align: right;<br />  border-right: 5px solid @blockquote-border-color;<br />  border-left: 0;<br /><br />  // Account for citation<br />  footer,<br />  small,<br />  .small {<br />    &:before { content: ""; }<br />    &:after {<br />      content: "\00A0 \2014"; // nbsp, em dash<br />    }<br />  }<br />}<br /><br />// Addresses<br />address {<br />  margin-bottom: @line-height-computed;<br />  font-style: normal;<br />  line-height: @line-height-base;<br />}<br />