ng;<br />  line-height: @line-height-base;<br />  background-color: @thumbnail-bg;<br />  border: 1px solid @thumbnail-border;<br />  border-radius: @thumbnail-border-radius;<br />  .transition(all .2s ease-in-out);<br /><br />  // Keep them at most 100% wide<br />  .img-responsive(inline-block);<br />}<br /><br />// Perfect circle<br />.img-circle {<br />  border-radius: 50%; // set radius in percents<br />}<br /><br /><br />// Horizontal rules<br /><br />hr {<br />  margin-top: @line-height-computed;<br />  margin-bottom: @line-height-computed;<br />  border: 0;<br />  border-top: 1px solid @hr-border;<br />}<br /><br /><br />// Only display content to screen readers<br />//<br />// See: https://a11yproject.com/posts/how-to-hide-content<br /><br />.sr-only {<br />  position: absolute;<br />  width: 1px;<br />  height: 1px;<br />  padding: 0;<br />  margin: -1px;<br />  overflow: hidden;<br />  clip: rect(0, 0, 0, 0);<br />  border: 0;<br />}<br /><br />// Use in conjunction with .sr-only to only display content when it's focused.<br />// Useful for "Skip to main content" links; see https://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1<br />// Credit: HTML5 Boilerplate<br /><br />.sr-only-focusable {<br />  &:active,<br />  &:focus {<br />    position: static;<br />    width: auto;<br />    height: auto;<br />    margin: 0;<br />    overflow: visible;<br />    clip: auto;<br />  }<br />}<br /><br /><br />// iOS "clickable elements" fix for role="button"<br />//<br />// Fixes "clickability" issue (and more generally, the firing of events such as focus as well)<br />// for traditionally non-focusable elements with role="button"<br />// see https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile<br /><br />[role="button"] {<br />  cursor: pointer;<br />}<br />