//<br />// Badges<br />// --------------------------------------------------<br /><br /><br />// Base class<br />.badge {<br />  display: inline-block;<br />  min-width: 10px;<br />  padding: 3px 7px;<br />  font-size: @font-size-small;<br />  font-weight: @badge-font-weight;<br />  line-height: @badge-line-height;<br />  color: @badge-color;<br />  text-align: center;<br />  white-space: nowrap;<br />  vertical-align: middle;<br />  background-color: @badge-bg;<br />  border-radius: @badge-border-radius;<br /><br />  // Empty badges collapse automatically (not available in IE8)<br />  &:empty {<br />    display: none;<br />  }<br /><br />  // Quick fix for badges in buttons<br />  .btn & {<br />    position: relative;<br />    top: -1px;<br />  }<br /><br />  .btn-xs &,<br />  .btn-group-xs > .btn & {<br />    top: 0;<br />    padding: 1px 5px;<br />  }<br /><br />  // Hover state, but only for links<br />  a& {<br />    &:hover,<br />    &:focus {<br />      color: @badge-link-hover-color;<br />      text-decoration: none;<br />      cursor: pointer;<br />    }<br />  }<br /><br />  // Account for badges in navs<br />  .list-group-item.active > &,<br />  .nav-pills > .active > a > & {<br />    color: @badge-active-color;<br />    background-color: @badge-active-bg;<br />  }<br /><br />  .list-group-item > & {<br />    float: right;<br />  }<br /><br />  .list-group-item > & + & {<br />    margin-right: 5px;<br />  }<br /><br />  .nav-pills > li > a > & {<br />    margin-left: 3px;<br />  }<br />}<br />