// stylelint-disable at-rule-no-vendor-prefix<br /><br />//<br />// Progress bars<br />// --------------------------------------------------<br /><br /><br />// Bar animations<br />// -------------------------<br /><br />// WebKit<br />@-webkit-keyframes progress-bar-stripes {<br />  from  { background-position: 40px 0; }<br />  to    { background-position: 0 0; }<br />}<br /><br />// Spec and IE10+<br />@keyframes progress-bar-stripes {<br />  from  { background-position: 40px 0; }<br />  to    { background-position: 0 0; }<br />}<br /><br /><br />// Bar itself<br />// -------------------------<br /><br />// Outer container<br />.progress {<br />  height: @line-height-computed;<br />  margin-bottom: @line-height-computed;<br />  overflow: hidden;<br />  background-color: @progress-bg;<br />  border-radius: @progress-border-radius;<br />  .box-shadow(inset 0 1px 2px rgba(0, 0, 0, .1));<br />}<br /><br />// Bar of progress<br />.progress-bar {<br />  float: left;<br />  width: 0%;<br />  height: 100%;<br />  font-size: @font-size-small;<br />  line-height: @line-height-computed;<br />  color: @progress-bar-color;<br />  text-align: center;<br />  background-color: @progress-bar-bg;<br />  .box-shadow(inset 0 -1px 0 rgba(0, 0, 0, .15));<br />  .transition(width .6s ease);<br />}<br /><br />// Striped bars<br />//<br />// `.progress-striped .progress-bar` is deprecated as of v3.2.0 in favor of the<br />// `.progress-bar-striped` class, which you just add to an existing<br />// `.progress-bar`.<br />.progress-striped .progress-bar,<br />.progress-bar-striped {<br />  #gradient > .striped();<br />  background-size: 40px 40px;<br />}<br /><br />// Call animation for the active one<br />//<br />// `.progress.active .progress-bar` is deprecated as of v3.2.0 in favor of the<br />// `.progress-bar.active` approach.<br />.progress.active .progress-bar,<br />.progress-bar.active {<br />  .animation(progress-bar-stripes 2s linear infinite);<br />}<br /><br /><br />// Variations<br />// -------------------------<br /><br />.progress-bar-success {<br />  .progress-bar-variant(@progress-bar-success-bg);<br />}<br /><br />.progress-bar-info {<br />  .progress-bar-variant(@progress-bar-info-bg);<br />}<br /><br />.progress-bar-warning {<br />  .progress-bar-variant(@progress-bar-warning-bg);<br />}<br /><br />.progress-bar-danger {<br />  .progress-bar-variant(@progress-bar-danger-bg);<br />}<br />