// stylelint-disable selector-max-type, selector-max-compound-selectors, selector-max-combinators, no-duplicate-selectors<br /><br />//<br />// Panels<br />// --------------------------------------------------<br /><br /><br />// Base class<br />.panel {<br />  margin-bottom: @line-height-computed;<br />  background-color: @panel-bg;<br />  border: 1px solid transparent;<br />  border-radius: @panel-border-radius;<br />  .box-shadow(0 1px 1px rgba(0, 0, 0, .05));<br />}<br /><br />// Panel contents<br />.panel-body {<br />  padding: @panel-body-padding;<br />  &:extend(.clearfix all);<br />}<br /><br />// Optional heading<br />.panel-heading {<br />  padding: @panel-heading-padding;<br />  border-bottom: 1px solid transparent;<br />  .border-top-radius((@panel-border-radius - 1));<br /><br />  > .dropdown .dropdown-toggle {<br />    color: inherit;<br />  }<br />}<br /><br />// Within heading, strip any `h*` tag of its default margins for spacing.<br />.panel-title {<br />  margin-top: 0;<br />  margin-bottom: 0;<br />  font-size: ceil((@font-size-base * 1.125));<br />  color: inherit;<br /><br />  > a,<br />  > small,<br />  > .small,<br />  > small > a,<br />  > .small > a {<br />    color: inherit;<br />  }<br />}<br /><br />// Optional footer (stays gray in every modifier class)<br />.panel-footer {<br />  padding: @panel-footer-padding;<br />  background-color: @panel-footer-bg;<br />  border-top: 1px solid @panel-inner-border;<br />  .border-bottom-radius((@panel-border-radius - 1));<br />}<br /><br /><br />// List groups in panels<br />//<br />// By default, space out list group content from panel headings to account for<br />// any kind of custom content between the two.<br /><br />.panel {<br />  > .list-group,<br />  > .panel-collapse > .list-group {<br />    margin-bottom: 0;<br /><br />    .list-group-item {<br />      border-width: 1px 0;<br />      border-radius: 0;<br />    }<br /><br />    // Add border top radius for first one<br />    &:first-child