/>  // Add border bottom radius for last one<br />  > .table:last-child,<br />  > .table-responsive:last-child > .table:last-child {<br />    .border-bottom-radius((@panel-border-radius - 1));<br /><br />    > tbody:last-child,<br />    > tfoot:last-child {<br />      > tr:last-child {<br />        border-bottom-right-radius: (@panel-border-radius - 1);<br />        border-bottom-left-radius: (@panel-border-radius - 1);<br /><br />        td:first-child,<br />        th:first-child {<br />          border-bottom-left-radius: (@panel-border-radius - 1);<br />        }<br />        td:last-child,<br />        th:last-child {<br />          border-bottom-right-radius: (@panel-border-radius - 1);<br />        }<br />      }<br />    }<br />  }<br />  > .panel-body + .table,<br />  > .panel-body + .table-responsive,<br />  > .table + .panel-body,<br />  > .table-responsive + .panel-body {<br />    border-top: 1px solid @table-border-color;<br />  }<br />  > .table > tbody:first-child > tr:first-child th,<br />  > .table > tbody:first-child > tr:first-child td {<br />    border-top: 0;<br />  }<br />  > .table-bordered,<br />  > .table-responsive > .table-bordered {<br />    border: 0;<br />    > thead,<br />    > tbody,<br />    > tfoot {<br />      > tr {<br />        > th:first-child,<br />        > td:first-child {<br />          border-left: 0;<br />        }<br />        > th:last-child,<br />        > td:last-child {<br />          border-right: 0;<br />        }<br />      }<br />    }<br />    > thead,<br />    > tbody {<br />      > tr:first-child {<br />        > td,<br />        > th {<br />          border-bottom: 0;<br />        }<br />      }<br />    }<br />    > tbody,<br />    > tfoot {<br />      > tr:last-child {<br />        > td,<br />        > th {<br />          border-bottom: 0;<br />        }<br />      }<br />    }<br />  }<br />  > .table-responsive {<br />    margin-bottom: 0;<br />    border: 0;<br />  }<br />}<br /><br /><br />// Collapsible