//<br />// Tooltips<br />// --------------------------------------------------<br /><br /><br />// Base class<br />.tooltip {<br />  position: absolute;<br />  z-index: @zindex-tooltip;<br />  display: block;<br />  // Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.<br />  // So reset our font and text properties to avoid inheriting weird values.<br />  .reset-text();<br />  font-size: @font-size-small;<br /><br />  .opacity(0);<br /><br />  &.in { .opacity(@tooltip-opacity); }<br />  &.top {<br />    padding: @tooltip-arrow-width 0;<br />    margin-top: -3px;<br />  }<br />  &.right {<br />    padding: 0 @tooltip-arrow-width;<br />    margin-left: 3px;<br />  }<br />  &.bottom {<br />    padding: @tooltip-arrow-width 0;<br />    margin-top: 3px;<br />  }<br />  &.left {<br />    padding: 0 @tooltip-arrow-width;<br />    margin-left: -3px;<br />  }<br /><br />  // Note: Deprecated .top-left, .top-right, .bottom-left, and .bottom-right as of v3.3.1<br />  &.top .tooltip-arrow {<br />    bottom: 0;<br />    left: 50%;<br />    margin-left: -@tooltip-arrow-width;<br />    border-width: @tooltip-arrow-width @tooltip-arrow-width 0;<br />    border-top-color: @tooltip-arrow-color;<br />  }<br />  &.top-left .tooltip-arrow {<br />    right: @tooltip-arrow-width;<br />    bottom: 0;<br />    margin-bottom: -@tooltip-arrow-width;<br />    border-width: @tooltip-arrow-width @tooltip-arrow-width 0;<br />    border-top-color: @tooltip-arrow-color;<br />  }<br />  &.top-right .tooltip-arrow {<br />    bottom: 0;<br />    left: @tooltip-arrow-width;<br />    margin-bottom: -@tooltip-arrow-width;<br />    border-width: @tooltip-arrow-width @tooltip-arrow-width 0;<br />    border-top-color: @tooltip-arrow-color;<br />  }<br />  &.right .tooltip-arrow {<br />    top: 50%;<br />    left: 0;<br />    margin-top: -@tooltip-arrow-width;<br />    border-width: @tooltip-arrow-width @tooltip-arrow-width @tooltip-arrow-width 0;<br />    border-right-color: @tooltip-arrow-color;<br />  }<br />  &.left .tooltip-arrow {<br />    top: 50%;<br />    right: 0;<br />    margin-top: -@tooltip-arrow-width;<br />    border-width: @tooltip-arrow-width 0 @tooltip-arrow-width @tooltip-arrow-width;<br />    border-left-color: @tooltip-arrow-color;<br />  }<br />  &.bottom .tooltip-arrow {<br />    top: 0;<br />    left: 50%;<br />    margin-left: -@tooltip-arrow-width;<br />    border-width: 0 @tooltip-arrow-width @tooltip-arrow-width;<br />    border-bottom-color: @tooltip-arrow-color;<br />  }<br />  &.bottom-left .tooltip-arrow {<br />    top: 0;<br />    right: @tooltip-arrow-width;<br />    margin-top: -@tooltip-arrow-width;<br />    border-width: 0 @tooltip-arrow-width @tooltip-arrow-width;<br />    border-bottom-color: @tooltip-arrow-color;<br />  }<br />  &.bottom-right .tooltip-arrow {<br />    top: 0;<br />    left: @tooltip-arrow-width;<br />    margin-top: -@tooltip-arrow-width;<br />    border-width: 0 @tooltip-arrow-width @tooltip-arrow-width;<br />    border-bottom-color: @tooltip-arrow-color;<br />  }<br />}<br /><br />// Wrapper for the tooltip content<br />.tooltip-inner {<br />  max-width: @tooltip-max-width;<br />  padding: 3px 8px;<br />  color: @tooltip-color;<br />  text-align: center;<br />  background-color: @tooltip-bg;<br />  border-radius: @border-radius-base;<br />}<br /><br />// Arrows<br />.tooltip-arrow {<br />  position: absolute;<br />  width: 0;<br />  height: 0;<br />  border-color: transparent;<br />  border-style: solid;<br />}<br />