//<br />// Breadcrumbs<br />// --------------------------------------------------<br /><br /><br />.breadcrumb {<br />  padding: @breadcrumb-padding-vertical @breadcrumb-padding-horizontal;<br />  margin-bottom: @line-height-computed;<br />  list-style: none;<br />  background-color: @breadcrumb-bg;<br />  border-radius: @border-radius-base;<br /><br />  > li {<br />    display: inline-block;<br /><br />    + li:before {<br />      padding: 0 5px;<br />      color: @breadcrumb-color;<br />      content: "@{breadcrumb-separator}\00a0"; // Unicode space added since inline-block means non-collapsing white-space<br />    }<br />  }<br /><br />  > .active {<br />    color: @breadcrumb-active-color;<br />  }<br />}<br />