//<br />// Labels<br />// --------------------------------------------------<br /><br />.label {<br />  display: inline;<br />  padding: .2em .6em .3em;<br />  font-size: 75%;<br />  font-weight: 700;<br />  line-height: 1;<br />  color: @label-color;<br />  text-align: center;<br />  white-space: nowrap;<br />  vertical-align: baseline;<br />  border-radius: .25em;<br /><br />  // Add hover effects, but only for links<br />  a& {<br />    &:hover,<br />    &:focus {<br />      color: @label-link-hover-color;<br />      text-decoration: none;<br />      cursor: pointer;<br />    }<br />  }<br /><br />  // Empty labels collapse automatically (not available in IE8)<br />  &:empty {<br />    display: none;<br />  }<br /><br />  // Quick fix for labels in buttons<br />  .btn & {<br />    position: relative;<br />    top: -1px;<br />  }<br />}<br /><br />// Colors<br />// Contextual variations (linked labels get darker on :hover)<br /><br />.label-default {<br />  .label-variant(@label-default-bg);<br />}<br /><br />.label-primary {<br />  .label-variant(@label-primary-bg);<br />}<br /><br />.label-success {<br />  .label-variant(@label-success-bg);<br />}<br /><br />.label-info {<br />  .label-variant(@label-info-bg);<br />}<br /><br />.label-warning {<br />  .label-variant(@label-warning-bg);<br />}<br /><br />.label-danger {<br />  .label-variant(@label-danger-bg);<br />}<br />