// stylelint-disable selector-no-qualifying-type<br /><br />//<br />// Thumbnails<br />// --------------------------------------------------<br /><br /><br />// Mixin and adjust the regular image class<br />.thumbnail {<br />  display: block;<br />  padding: @thumbnail-padding;<br />  margin-bottom: @line-height-computed;<br />  line-height: @line-height-base;<br />  background-color: @thumbnail-bg;<br />  border: 1px solid @thumbnail-border;<br />  border-radius: @thumbnail-border-radius;<br />  .transition(border .2s ease-in-out);<br /><br />  > img,<br />  a > img {<br />    &:extend(.img-responsive);<br />    margin-right: auto;<br />    margin-left: auto;<br />  }<br /><br />  // Add a hover state for linked versions only<br />  a&:hover,<br />  a&:focus,<br />  a&.active {<br />    border-color: @link-color;<br />  }<br /><br />  // Image captions<br />  .caption {<br />    padding: @thumbnail-caption-padding;<br />    color: @thumbnail-caption-color;<br />  }<br />}<br />