//<br />// Alerts<br />// --------------------------------------------------<br /><br /><br />// Base styles<br />// -------------------------<br /><br />.alert {<br />  padding: @alert-padding;<br />  margin-bottom: @line-height-computed;<br />  border: 1px solid transparent;<br />  border-radius: @alert-border-radius;<br /><br />  // Headings for larger alerts<br />  h4 {<br />    margin-top: 0;<br />    color: inherit; // Specified for the h4 to prevent conflicts of changing @headings-color<br />  }<br /><br />  // Provide class for links that match alerts<br />  .alert-link {<br />    font-weight: @alert-link-font-weight;<br />  }<br /><br />  // Improve alignment and spacing of inner content<br />  > p,<br />  > ul {<br />    margin-bottom: 0;<br />  }<br /><br />  > p + p {<br />    margin-top: 5px;<br />  }<br />}<br /><br />// Dismissible alerts<br />//<br />// Expand the right padding and account for the close button's positioning.<br /><br />// The misspelled .alert-dismissable was deprecated in 3.2.0.<br />.alert-dismissable,<br />.alert-dismissible {<br />  padding-right: (@alert-padding + 20);<br /><br />  // Adjust close link position<br />  .close {<br />    position: relative;<br />    top: -2px;<br />    right: -21px;<br />    color: inherit;<br />  }<br />}<br /><br />// Alternate styles<br />//<br />// Generate contextual modifier classes for colorizing the alert.<br /><br />.alert-success {<br />  .alert-variant(@alert-success-bg; @alert-success-border; @alert-success-text);<br />}<br /><br />.alert-info {<br />  .alert-variant(@alert-info-bg; @alert-info-border; @alert-info-text);<br />}<br /><br />.alert-warning {<br />  .alert-variant(@alert-warning-bg; @alert-warning-border; @alert-warning-text);<br />}<br /><br />.alert-danger {<br />  .alert-variant(@alert-danger-bg; @alert-danger-border; @alert-danger-text);<br />}<br />