// stylelint-disable media-feature-name-no-unknown<br /><br />//<br />// Carousel<br />// --------------------------------------------------<br /><br /><br />// Wrapper for the slide container and indicators<br />.carousel {<br />  position: relative;<br />}<br /><br />.carousel-inner {<br />  position: relative;<br />  width: 100%;<br />  overflow: hidden;<br /><br />  > .item {<br />    position: relative;<br />    display: none;<br />    .transition(.6s ease-in-out left);<br /><br />    // Account for jankitude on images<br />    > img,<br />    > a > img {<br />      &:extend(.img-responsive);<br />      line-height: 1;<br />    }<br /><br />    // WebKit CSS3 transforms for supported devices<br />    @media all and (transform-3d), (-webkit-transform-3d) {<br />      .transition-transform(~"0.6s ease-in-out");<br />      .backface-visibility(~"hidden");<br />      .perspective(1000px);<br /><br />      &.next,<br />      &.active.right {<br />        .translate3d(100%, 0, 0);<br />        left: 0;<br />      }<br />      &.prev,<br />      &.active.left {<br />        .translate3d(-100%, 0, 0);<br />        left: 0;<br />      }<br />      &.next.left,<br />      &.prev.right,<br />      &.active {<br />        .translate3d(0, 0, 0);<br />        left: 0;<br />      }<br />    }<br />  }<br /><br />  > .active,<br />  > .next,<br />  > .prev {<br />    display: block;<br />  }<br /><br />  > .active {<br />    left: 0;<br />  }<br /><br />  > .next,<br />  > .prev {<br />    position: absolute;<br />    top: 0;<br />    width: 100%;<br />  }<br /><br />  > .next {<br />    left: 100%;<br />  }<br />  > .prev {<br />    left: -100%;<br />  }<br />  > .next.left,<br />  > .prev.right {<br />    left: 0;<br />  }<br /><br />  > .active.left {<br />    left: -100%;<br />  }<br />  > .active.right {<br />    left: 100%;<br />  }<br /><br />}<br /><br />// Left/right controls for nav<br />// ---------------------------<br /><br />.carousel-control {<br />  position: absolute;<br />  top: 0;<br />  bottom: 0;<br />  left: 0;<br />  width: @carousel-control-width;<br />  font-size: @carousel-control-font-size;<br />  color: @carousel-control-color;<br />  text-align: center;<br />  text-shadow: @carousel-text-shadow;<br />  background-color: rgba(0, 0, 0, 0); // Fix IE9 click-thru bug<br />  .opacity(@carousel-control-opacity);<br />  // We can't have this transition here because WebKit cancels the carousel<br />  // animation if you trip this while in the middle of another animation.<br /><br />  // Set gradients for backgrounds<br />  &.left {<br />    #gradient > .horizontal(@start-color: rgba(0, 0, 0, .5); @end-color: rgba(0, 0, 0, .0001));<br />  }<br />  &.right {<br />    right: 0;<br />    left: auto;<br />    #gradient > .horizontal(@start-color: rgba(0, 0, 0, .0001); @end-color: rgba(0, 0, 0, .5));<br />  }<br /><br />  // Hover/focus state<br />  &:hover,<br />  &:focus {<br />    color: @carousel-control-color;<br />    text-decoration: none;<br />    outline: 0;<br />    .opacity(.9);<br />  }<br /><br />  // Toggles<br />  .icon-prev,<br />  .icon-next,<br />  .glyphicon-chevron-left,<br />  .glyphicon-chevron-right {<br />    position: absolute;<br />    top: 50%;<br />    z-index: 5;<br />    display: inline-block;<br />    margin-top: -10px;<br />  }<br />  .icon-prev,<br />  .glyphicon-chevron-left {<br />    left: 50%;<br />    margin-left: -10px;<br />  }<br />  .icon-next,<br />  .glyphicon-chevron-right {<br />    right: 50%;<br />    margin-right: -10px;<br />  }<br />  .icon-prev,<br />  .icon-next {<br />    width: 20px;<br />    height: 20px;<br />    font-family: serif;<br />    line-height: 1;<br />  }<br /><br />  .icon-prev {<br />    &:before {<br />      content: "\2039";// SINGLE LEFT-POINTING ANGLE QUOTATION MARK (U+2039)<br />    }<br />  }<br />  .icon-next {<br />    &:before {<br />      content: "\203a";// SINGLE RIGHT-POINTING ANGLE QUOTATION MARK (U+203A)<br />    }<br />  }<br />}<br /><br />// Optional indicator pips<br />//<br />// Add an unordered list with the following class and add a list item for each<br />// slide your carousel holds.<br /><br />.carousel-indicators {<br />  position: absolute;<br />  bottom: 10px;<br />  left: 50%;<br />  z-index: 15;<br />  width: 60%;<br />  padding-left: 0;<br />  margin-left: -30%;<br />  text-align: center;<br />  list-style: none;<br /><br />  li {<br />    display: inline-block;<br />    width: 10px;<br />    height: 10px;<br />    margin: 1px;<br />    text-indent: -999px;<br />    cursor: pointer;<br />    // IE8-9 hack for event handling<br />    //<br />    // Internet Explorer 8-9 does not support clicks on elements without a set<br />    // `background-color`. We cannot use `filter` since that's not viewed as a<br />    // background color by the browser. Thus, a hack is needed.<br />    // See https://developer.mozilla.org/en-US/docs/Web/Events/click#Internet_Explorer<br />    //<br />    // For IE8, we set solid black as it doesn't support `rgba()`. For IE9, we<br />    // set alpha transparency for the best results possible.<br />    background-color: #000 \9; // IE8<br />    background-color: rgba(0, 0, 0, 0); // IE9<br /><br />    border: 1px solid @carousel-indicator-border-color;<br />    border-radius: 10px;<br />  }<br /><br />  .active {<br />    width: 12px;<br />    height: 12px;<br />    margin: 0;<br />    background-color: @carousel-indicator-active-bg;<br />  }<br />}<br /><br />// Optional captions<br />// -----------------------------<br />// Hidden by default for smaller viewports<br />.carousel-caption {<br />  position: absolute;<br />  right: 15%;<br />  bottom: 20px;<br />  left: 15%;<br />  z-index: 10;<br />  padding-top: 20px;<br />  padding-bottom: 20px;<br />  color: @carousel-caption-color;<br />  text-align: center;<br />  text-shadow: @carousel-text-shadow;<br /><br />  & .btn {<br />    text-shadow: none; // No shadow for button elements in carousel-caption<br />  }<br />}<br /><br /><br />// Scale up controls for tablets and up<br />@media screen and (min-width: @screen-sm-min) {<br /><br />  // Scale up the controls a smidge<br />  .carousel-control {<br />    .glyphicon-chevron-left,<br />    .glyphicon-chevron-right,<br />    .icon-prev,<br />    .icon-next {<br />      width: (@carousel-control-font-size * 1.5);<br />      height: (@carousel-control-font-size * 1.5);<br />      margin-top: (@carousel-control-font-size / -2);<br />      font-size: (@carousel-control-font-size * 1.5);<br />    }<br />    .glyphicon-chevron-left,<br />    .icon-prev {<br />      margin-left: (@carousel-control-font-size / -2);<br />    }<br />    .glyphicon-chevron-right,<br />    .icon-next {<br />      margin-right: (@carousel-control-font-size / -2);<br />    }<br />  }<br /><br />  // Show and left align the captions<br />  .carousel-caption {<br />    right: 20%;<br />    left: 20%;<br />    padding-bottom: 30px;<br />  }<br /><br />  // Move up the indicators<br />  .carousel-indicators {<br />    bottom: 20px;<br />  }<br />}<br />