// Framework grid generation<br />//<br />// Used only by Bootstrap to generate the correct number of grid classes given<br />// any value of `@grid-columns`.<br /><br />.make-grid-columns() {<br />  // Common styles for all sizes of grid columns, widths 1-12<br />  .col(@index) { // initial<br />    @item: ~".col-xs-@{index}, .col-sm-@{index}, .col-md-@{index}, .col-lg-@{index}";<br />    .col((@index + 1), @item);<br />  }<br />  .col(@index, @list) when (@index =< @grid-columns) { // general; "=<" isn't a typo<br />    @item: ~".col-xs-@{index}, .col-sm-@{index}, .col-md-@{index}, .col-lg-@{index}";<br />    .col((@index + 1), ~"@{list}, @{item}");<br />  }<br />  .col(@index, @list) when (@index > @grid-columns) { // terminal<br />    @{list} {<br />      position: relative;<br />      // Prevent columns from collapsing when empty<br />      min-height: 1px;<br />      // Inner gutter via padding<br />      padding-right: floor((@grid-gutter-width / 2));<br />      padding-left: ceil((@grid-gutter-width / 2));<br />    }<br />  }<br />  .col(1); // kickstart it<br />}<br /><br />.float-grid-columns(@class) {<br />  .col(@index) { // initial<br />    @item: ~".col-@{class}-@{index}";<br />    .col((@index + 1), @item);<br />  }<br />  .col(@index, @list) when (@index =< @grid-columns) { // general<br />    @item: ~".col-@{class}-@{index}";<br />    .col((@index + 1), ~"@{list}, @{item}");<br />  }<br />  .col(@index, @list) when (@index > @grid-columns) { // terminal<br />    @{list} {<br />      float: left;<br />    }<br />  }<br />  .col(1); // kickstart it<br />}<br /><br />.calc-grid-column(@index, @class, @type) when (@type = width) and (@index > 0) {<br />  .col-@{class}-@{index} {<br />    width: percentage((@index / @grid-columns));<br />  }<br />}<br />.calc-grid-column(@index, @class, @type) when (@type = push) and (@index > 0) {<br />  .col-@{class}-push-@{index} {<br />    left: percentage((@index / @grid-columns));<br />  }<br />}<br />.calc-grid-column(@index, @class, @type) when (@type = push) and (@index = 0) {<br />  .col-@{class}-push-0 {<br />    left: auto;<br />  }<br />}<br />.calc-grid-column(@index, @class, @type) when (@type = pull) and (@index > 0) {<br />  .col-@{class}-pull-@{index} {<br />    right: percentage((@index / @grid-columns));<br />  }<br />}<br />.calc-grid-column(@index, @class, @type) when (@type = pull) and (@index = 0) {<br />  .col-@{class}-pull-0 {<br />    right: auto;<br />  }<br />}<br />.calc-grid-column(@index, @class, @type) when (@type = offset) {<br />  .col-@{class}-offset-@{index} {<br />    margin-left: percentage((@index / @grid-columns));<br />  }<br />}<br /><br />// Basic looping in LESS<br />.loop-grid-columns(@index, @class, @type) when (@index >= 0) {<br />  .calc-grid-column(@index, @class, @type);<br />  // next iteration<br />  .loop-grid-columns((@index - 1), @class, @type);<br />}<br /><br />// Create grid for specific class<br />.make-grid(@class) {<br />  .float-grid-columns(@class);<br />  .loop-grid-columns(@grid-columns, @class, width);<br />  .loop-grid-columns(@grid-columns, @class, pull);<br />  .loop-grid-columns(@grid-columns, @class, push);<br />  .loop-grid-columns(@grid-columns, @class, offset);<br />}<br />