//<br />// Modals<br />// --------------------------------------------------<br /><br />// .modal-open      - body class for killing the scroll<br />// .modal           - container to scroll within<br />// .modal-dialog    - positioning shell for the actual modal<br />// .modal-content   - actual modal w/ bg and corners and shit<br /><br />// Kill the scroll on the body<br />.modal-open {<br />  overflow: hidden;<br />}<br /><br />// Container that the modal scrolls within<br />.modal {<br />  position: fixed;<br />  top: 0;<br />  right: 0;<br />  bottom: 0;<br />  left: 0;<br />  z-index: @zindex-modal;<br />  display: none;<br />  overflow: hidden;<br />  -webkit-overflow-scrolling: touch;<br /><br />  // Prevent Chrome on Windows from adding a focus outline. For details, see<br />  // https://github.com/twbs/bootstrap/pull/10951.<br />  outline: 0;<br /><br />  // When fading in the modal, animate it to slide down<br />  &.fade .modal-dialog {<br />    .translate(0, -25%);<br />    .transition-transform(~"0.3s ease-out");<br />  }<br />  &.in .modal-dialog { .translate(0, 0); }<br />}<br />.modal-open .modal {<br />  overflow-x: hidden;<br />  overflow-y: auto;<br />}<br /><br />// Shell div to position the modal with bottom padding<br />.modal-dialog {<br />  position: relative;<br />  width: auto;<br />  margin: 10px;<br />}<br /><br />// Actual modal<br />.modal-content {<br />  position: relative;<br />  background-color: @modal-content-bg;<br />  background-clip: padding-box;<br />  border: 1px solid @modal-content-fallback-border-color; //old browsers fallback (ie8 etc)<br />  border: 1px solid @modal-content-border-color;<br />  border-radius: @border-radius-large;<br />  .box-shadow(0 3px 9px rgba(0, 0, 0, .5));<br />  // Remove focus outline from opened modal<br />  outline: 0;<br />}<br /><br />// Modal background<br />.modal-backdrop {<br />  position: fixed;<br />  top: 0;<br />  right: 0;<br />  bottom: 0;<br />  left: 0;<br />  z-index: @zindex-modal-background;<br />  background-color: @modal-backdrop-bg;<br />  // Fade for backdrop<br />  &.fade { .opacity(0); }<br />  &.in { .opacity(@modal-backdrop-opacity); }<br />}<br /><br />// Modal header<br />// Top section of the modal w/ title and dismiss<br />.modal-header {<br />  padding: @modal-title-padding;<br />  border-bottom: 1px solid @modal-header-border-color;<br />  &:extend(.clearfix all);<br />}<br />// Close icon<br />.modal-header .close {<br />  margin-top: -2px;<br />}<br /><br />// Title text within header<br />.modal-title {<br />  margin: 0;<br />  line-height: @modal-title-line-height;<br />}<br /><br />// Modal body<br />// Where all modal content resides (sibling of .modal-header and .modal-footer)<br />.modal-body {<br />  position: relative;<br />  padding: @modal-inner-padding;<br />}<br /><br />// Footer (for actions)<br />.modal-footer {<br />  padding: @modal-inner-padding;<br />  text-align: right; // right align buttons<br />  border-top: 1px solid @modal-footer-border-color;<br />  &:extend(.clearfix all); // clear it in case folks use .pull-* classes on buttons<br /><br />  // Properly space out buttons<br />  .btn + .btn {<br />    margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs<br />    margin-left: 5px;<br />  }<br />  // but override that for button groups<br />  .btn-group .btn + .btn {<br />    margin-left: -1px;<br />  }<br />  // and override it for block buttons as well<br />  .btn-block + .btn-block {<br />    margin-left: 0;<br />  }<br />}<br /><br />// Measure scrollbar width for padding body during modal show/hide<br />.modal-scrollbar-measure {<br />  position: absolute;<br />  top: -9999px;<br />  width: 50px;<br />  height: 50px;<br />  overflow: scroll;<br />}<br /><br />// Scale up the modal<br />@media (min-width: @screen-sm-min) {<br />  // Automatically set modal's width for larger viewports<br />  .modal-dialog {<br />    width: @modal-md;<br />    margin: 30px auto;<br />  }<br />  .modal-content {<br />    .box-shadow(0 5px 15px rgba(0, 0, 0, .5));<br />  }<br /><br />  // Modal sizes<br />  .modal-sm { width: @modal-sm; }<br />}<br /><br />@media (min-width: @screen-md-min) {<br />  .modal-lg { width: @modal-lg; }<br />}<br />