//<br />// Pager pagination<br />// --------------------------------------------------<br /><br /><br />.pager {<br />  padding-left: 0;<br />  margin: @line-height-computed 0;<br />  text-align: center;<br />  list-style: none;<br />  &:extend(.clearfix all);<br />  li {<br />    display: inline;<br />    > a,<br />    > span {<br />      display: inline-block;<br />      padding: 5px 14px;<br />      background-color: @pager-bg;<br />      border: 1px solid @pager-border;<br />      border-radius: @pager-border-radius;<br />    }<br /><br />    > a:hover,<br />    > a:focus {<br />      text-decoration: none;<br />      background-color: @pager-hover-bg;<br />    }<br />  }<br /><br />  .next {<br />    > a,<br />    > span {<br />      float: right;<br />    }<br />  }<br /><br />  .previous {<br />    > a,<br />    > span {<br />      float: left;<br />    }<br />  }<br /><br />  .disabled {<br />    > a,<br />    > a:hover,<br />    > a:focus,<br />    > span {<br />      color: @pager-disabled-color;<br />      cursor: @cursor-disabled;<br />      background-color: @pager-bg;<br />    }<br />  }<br />}<br />