><br />.radio + .radio,<br />.checkbox + .checkbox {<br />  margin-top: -5px; // Move up sibling radios or checkboxes for tighter spacing<br />}<br /><br />// Radios and checkboxes on same line<br />.radio-inline,<br />.checkbox-inline {<br />  position: relative;<br />  display: inline-block;<br />  padding-left: 20px;<br />  margin-bottom: 0;<br />  font-weight: 400;<br />  vertical-align: middle;<br />  cursor: pointer;<br /><br />  // These are used directly on <label>s<br />  &.disabled,<br />  fieldset[disabled] & {<br />    cursor: @cursor-disabled;<br />  }<br />}<br />.radio-inline + .radio-inline,<br />.checkbox-inline + .checkbox-inline {<br />  margin-top: 0;<br />  margin-left: 10px; // space out consecutive inline controls<br />}<br /><br /><br />// Static form control text<br />//<br />// Apply class to a `p` element to make any string of text align with labels in<br />// a horizontal form layout.<br /><br />.form-control-static {<br />  min-height: (@line-height-computed + @font-size-base);<br />  // Size it appropriately next to real form controls<br />  padding-top: (@padding-base-vertical + 1);<br />  padding-bottom: (@padding-base-vertical + 1);<br />  // Remove default margin from `p`<br />  margin-bottom: 0;<br /><br />  &.input-lg,<br />  &.input-sm {<br />    padding-right: 0;<br />    padding-left: 0;<br />  }<br />}<br /><br /><br />// Form control sizing<br />//<br />// Build on `.form-control` with modifier classes to decrease or increase the<br />// height and font-size of form controls.<br />//<br />// The `.form-group-* form-control` variations are sadly duplicated to avoid the<br />// issue documented in https://github.com/twbs/bootstrap/issues/15074.<br /><br />.input-sm {<br />  .input-size(@input-height-small; @padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @input-border-radius-small);<br />}<br />.form-group-sm {<br />  .form-control {<br />    height: @input-height-small;<br />    padding