ing help-block<br />  margin-top: 5px;<br />  margin-bottom: 10px;<br />  color: lighten(@text-color, 25%); // lighten the text some for contrast<br />}<br /><br /><br />// Inline forms<br />//<br />// Make forms appear inline(-block) by adding the `.form-inline` class. Inline<br />// forms begin stacked on extra small (mobile) devices and then go inline when<br />// viewports reach <768px.<br />//<br />// Requires wrapping inputs and labels with `.form-group` for proper display of<br />// default HTML form controls and our custom form controls (e.g., input groups).<br />//<br />// Heads up! This is mixin-ed into `.navbar-form` in navbars.less.<br /><br />.form-inline {<br /><br />  // Kick in the inline<br />  @media (min-width: @screen-sm-min) {<br />    // Inline-block all the things for "inline"<br />    .form-group {<br />      display: inline-block;<br />      margin-bottom: 0;<br />      vertical-align: middle;<br />    }<br /><br />    // In navbar-form, allow folks to *not* use `.form-group`<br />    .form-control {<br />      display: inline-block;<br />      width: auto; // Prevent labels from stacking above inputs in `.form-group`<br />      vertical-align: middle;<br />    }<br /><br />    // Make static controls behave like regular ones<br />    .form-control-static {<br />      display: inline-block;<br />    }<br /><br />    .input-group {<br />      display: inline-table;<br />      vertical-align: middle;<br /><br />      .input-group-addon,<br />      .input-group-btn,<br />      .form-control {<br />        width: auto;<br />      }<br />    }<br /><br />    // Input groups need that 100% width though<br />    .input-group > .form-control {<br />      width: 100%;<br />    }<br /><br />    .control-label {<br />      margin-bottom: 0;<br />      vertical-align: middle;<br />    }<br /><br />    // Remove default margin on radios/checkboxes that were used for stacking, and<br />    // then undo the floating of radios and checkboxes to match.<br />