nd columns have<br />  // `position: relative;` on them. Also accounts for the grid gutter padding.<br />  .has-feedback .form-control-feedback {<br />    right: floor((@grid-gutter-width / 2));<br />  }<br /><br />  // Form group sizes<br />  //<br />  // Quick utility class for applying `.input-lg` and `.input-sm` styles to the<br />  // inputs and labels within a `.form-group`.<br />  .form-group-lg {<br />    @media (min-width: @screen-sm-min) {<br />      .control-label {<br />        padding-top: (@padding-large-vertical + 1);<br />        font-size: @font-size-large;<br />      }<br />    }<br />  }<br />  .form-group-sm {<br />    @media (min-width: @screen-sm-min) {<br />      .control-label {<br />        padding-top: (@padding-small-vertical + 1);<br />        font-size: @font-size-small;<br />      }<br />    }<br />  }<br />}<br />