// Tables<br /><br />.table-row-variant(@state; @background) {<br />  // Exact selectors below required to override `.table-striped` and prevent<br />  // inheritance to nested tables.<br />  .table > thead > tr,<br />  .table > tbody > tr,<br />  .table > tfoot > tr {<br />    > td.@{state},<br />    > th.@{state},<br />    &.@{state} > td,<br />    &.@{state} > th {<br />      background-color: @background;<br />    }<br />  }<br /><br />  // Hover states for `.table-hover`<br />  // Note: this is not available for cells or rows within `thead` or `tfoot`.<br />  .table-hover > tbody > tr {<br />    > td.@{state}:hover,<br />    > th.@{state}:hover,<br />    &.@{state}:hover > td,<br />    &:hover > .@{state},<br />    &.@{state}:hover > th {<br />      background-color: darken(@background, 5%);<br />    }<br />  }<br />}<br />