// Clearfix<br />//<br />// For modern browsers<br />// 1. The space content is one way to avoid an Opera bug when the<br />//    contenteditable attribute is included anywhere else in the document.<br />//    Otherwise it causes space to appear at the top and bottom of elements<br />//    that are clearfixed.<br />// 2. The use of `table` rather than `block` is only necessary if using<br />//    `:before` to contain the top-margins of child elements.<br />//<br />// Source: http://nicolasgallagher.com/micro-clearfix-hack/<br /><br />.clearfix() {<br />  &:before,<br />  &:after {<br />    display: table; // 2<br />    content: " "; // 1<br />  }<br />  &:after {<br />    clear: both;<br />  }<br />}<br />