//<br />// Pagination (multiple pages)<br />// --------------------------------------------------<br />.pagination {<br />  display: inline-block;<br />  padding-left: 0;<br />  margin: @line-height-computed 0;<br />  border-radius: @border-radius-base;<br /><br />  > li {<br />    display: inline; // Remove list-style and block-level defaults<br />    > a,<br />    > span {<br />      position: relative;<br />      float: left; // Collapse white-space<br />      padding: @padding-base-vertical @padding-base-horizontal;<br />      margin-left: -1px;<br />      line-height: @line-height-base;<br />      color: @pagination-color;<br />      text-decoration: none;<br />      background-color: @pagination-bg;<br />      border: 1px solid @pagination-border;<br /><br />      &:hover,<br />      &:focus {<br />        z-index: 2;<br />        color: @pagination-hover-color;<br />        background-color: @pagination-hover-bg;<br />        border-color: @pagination-hover-border;<br />      }<br />    }<br />    &:first-child {<br />      > a,<br />      > span {<br />        margin-left: 0;<br />        .border-left-radius(@border-radius-base);<br />      }<br />    }<br />    &:last-child {<br />      > a,<br />      > span {<br />        .border-right-radius(@border-radius-base);<br />      }<br />    }<br />  }<br /><br />  > .active > a,<br />  > .active > span {<br />    &,<br />    &:hover,<br />    &:focus {<br />      z-index: 3;<br />      color: @pagination-active-color;<br />      cursor: default;<br />      background-color: @pagination-active-bg;<br />      border-color: @pagination-active-border;<br />    }<br />  }<br /><br />  > .disabled {<br />    > span,<br />    > span:hover,<br />    > span:focus,<br />    > a,<br />    > a:hover,<br />    > a:focus {<br />      color: @pagination-disabled-color;<br />      cursor: @cursor-disabled;<br />      background-color: @pagination-disabled-bg;<br />      border-color: @pagination-disabled-border;<br />    }<br />  }<br />}<br /><br />// Sizing<br />// --------------------------------------------------<br /><br />// Large<br />.pagination-lg {<br />  .pagination-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @border-radius-large);<br />}<br /><br />// Small<br />.pagination-sm {<br />  .pagination-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @border-radius-small);<br />}<br />