// Navbar vertical align<br />//<br />// Vertically center elements in the navbar.<br />// Example: an element has a height of 30px, so write out `.navbar-vertical-align(30px);` to calculate the appropriate top margin.<br /><br />.navbar-vertical-align(@element-height) {<br />  margin-top: ((@navbar-height - @element-height) / 2);<br />  margin-bottom: ((@navbar-height - @element-height) / 2);<br />}<br />