// Pagination<br /><br />.pagination-size(@padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius) {<br />  > li {<br />    > a,<br />    > span {<br />      padding: @padding-vertical @padding-horizontal;<br />      font-size: @font-size;<br />      line-height: @line-height;<br />    }<br />    &:first-child {<br />      > a,<br />      > span {<br />        .border-left-radius(@border-radius);<br />      }<br />    }<br />    &:last-child {<br />      > a,<br />      > span {<br />        .border-right-radius(@border-radius);<br />      }<br />    }<br />  }<br />}<br />